.jumbo {
    height: 450px;
    background: -webkit-gradient(
        linear,
        left top, left bottom,
        from(rgba(0, 0, 0, 0.7)),
        to(rgba(0, 0, 0, 0.7))
      ),
      url('http://esconrussia.ru/wp-content/uploads/2018/04/main-front-racks.jpg') no-repeat;
    background: -webkit-linear-gradient(
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.7)
      ),
      url('http://esconrussia.ru/wp-content/uploads/2018/04/main-front-racks.jpg') no-repeat;
    background: -o-linear-gradient(
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.7)
      ),
      url('http://esconrussia.ru/wp-content/uploads/2018/04/main-front-racks.jpg') no-repeat;
    background: linear-gradient(
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.7)
      ),
      url('http://esconrussia.ru/wp-content/uploads/2018/04/main-front-racks.jpg') no-repeat;
    background-size: cover;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
}

.jumbo-text-container {
    max-width: 902px;
}

.jumbo-title {
    font-size: 48px;
    color: #fff;
}

.jumbo-subtitle {
    font-size: 36px;
    color: #fff;
}


